.hide {
  display: none;
}

.imageW {
  width: 600px;
  height: 300px;
  background-color: #9608a5;
  border-radius: 10px;
}

.imageHV {
  height: 425px;
  width: 300px;
  border-radius: 10px;
}
.imageH {
  height: 425px;
  width: 250px;
  border-radius: 10px;
}

.man-nav {
  position: absolute;
  width: 1000px;
  margin-top: 560px;
  display: flex;
  justify-content: center;
}

.man-button {
  border: 2px solid red;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.5s;
}

.man-button:not(:last-child) {
  margin-right: 40px;
}

.man-button:hover {
  background: red;
}

#radio-1:checked ~ .first {
  margin-left: 0;
}

#radio-2:checked ~ .first {
  margin-left: -20%;
}

#radio-3:checked ~ .first {
  margin-left: -40%;
}

#radio-4:checked ~ .first {
  margin-left: -60%;
}

.auto-nav {
  position: absolute;
  display: flex;
  width: 1000px;
  justify-content: center;
  margin-top: 560px;
}

.auto-nav div {
  border: 2px solid black;
  padding: 5px;
  border-radius: 10px;
  transition: 0.5s;
}

.auto-nav div:not(:last-child) {
  margin-right: 40px;
}

#radio-1:checked ~ .auto-nav .auto1 {
  background: red;
}

#radio-2:checked ~ .auto-nav .auto2 {
  background: red;
}

#radio-3:checked ~ .auto-nav .auto3 {
  background: red;
}

#radio-4:checked ~ .auto-nav .auto4 {
  background: red;
}

.carousel-button {
  display: flex;
  justify-content: space-evenly;
}

.arrows {
  cursor: pointer;

}

.education {
  text-align: center;
}

.university {
  width: 100%;
  height: calc(100vh);
  background: url('./img/IMG_3011.jpg');
  background-size: cover;
  text-align: center;
  position: relative;
  top: 0;
}

.university h1 {
  padding-top: 450px;
  font-size: 100px;
  color: white;
}

.imaging {
  margin-top: 10px;
  border-radius: 15px;
  background-size: cover;
}

.Eng {
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 50px;
  margin-right: 50px;
}

.passions {
  text-align: center;
  justify-content: center;
  margin-bottom: 150px;
}

.passion {
  display: flex;
  flex-direction: row;
  margin-right: 50px;
  margin-left: 50px;
  justify-content: space-evenly;
}

.passion-item {
  height: 500px;
  background-color: beige;
